    <DoubleLogeMana />
      <table class="management-box" @click.prevent="changeStatus"> 
        <tr>
          <td><img src="../assets/images/online_melaleuca.jpg" alt=""></td>
          <td>Melaleuca</td>
          <td>AUD 50</td>
          <td v-show="flag&&flag11">INSTOCK</td>
          <td v-show="flag&&!flag11">SOLDOUT</td>
          <td v-show="!flag" id="f11">
            <button :class="flag11?'active1':''">INSTOCK</button>
            <button :class="!flag11?'active1':''">SOLDOUT</button>
          </td>

        </tr>
        <tr>
          <td><img src="../assets/images/online_cheesecake.jpg" alt=""></td>
          <td>Cheesecake</td>
