          </td>
        </tr>
        <tr>
          <td><img src="../assets/images/online_cupcake.jpg" alt=""></td>
          <td>Cup cake</td>
          <td>AUD 30</td>
          <td v-show="flag&&flag33">INSTOCK</td>
          <td v-show="flag&&!flag33">SOLDOUT</td>
          <td v-show="!flag" id="f33">
            <button :class="flag33?'active1':''">INSTOCK</button>
            <button :class="!flag33?'active1':''">SOLDOUT</button>
          </td>
        </tr>
        <tr>
          <td><img src="../assets/images/online_toiletpapercake.jpg" alt=""></td>
          <td>Creative toilet paper cake</td>
          <td>AUD 65</td>
          <td v-show="flag&&flag44">INSTOCK</td>
          <td v-show="flag&&!flag44">SOLDOUT</td>
          <td v-show="!flag" id="f44">
            <button :class="flag44?'active1':''">INSTOCK</button>
            <button :class="!flag44?'active1':''">SOLDOUT</button>
          </td>
        </tr>
        <tr>
          <td><img src="../assets/images/online_tiane.jpg" alt=""></td>
          <td>Special custom black swan cake</td>
          <td>AUD 120</td>
          <td v-show="flag&&flag55">INSTOCK</td>
          <td v-show="flag&&!flag55">SOLDOUT</td>
          <td v-show="!flag" id="f55">
            <button :class="flag55?'active1':''">INSTOCK</button>
            <button :class="!flag55?'active1':''">SOLDOUT</button>
          </td>
        </tr>
        <tr>
