          <td>Fruit birthday cake</td>
          <td>AUD 80</td>
          <td v-show="flag&&flag66">INSTOCK</td>
          <td v-show="flag&&!flag66">SOLDOUT</td>
          <td v-show="!flag" id="f66">
            <button :class="flag66?'active1':''">INSTOCK</button>
            <button :class="!flag66?'active1':''">SOLDOUT</button>
          </td>
        </tr>
      </table>
      <div class="editbtnbg">
        <div class="editbtn" @click.prevent="changeEdit" v-show="flag"><a href="">EDIT</a></div>
        <div class="editbtn" @click.prevent="changeEdit" v-show="!flag"><a href="">DONE</a></div>
      </div>
      
  </div>
</template>

<script>
import HomeBgimg from '../components/HomeBgimg'
import DoubleLogeMana from '../components/DoubleLogoMana'
export default {
  components:{
    HomeBgimg,
    DoubleLogeMana
  },
  data(){
    return {
      flag:true,
      flag11:JSON.parse(window.localStorage.getItem('flag11')),
      flag22:JSON.parse(window.localStorage.getItem('flag22')),
      flag33:JSON.parse(window.localStorage.getItem('flag33')),
      flag44:JSON.parse(window.localStorage.getItem('flag44')),
      flag55:JSON.parse(window.localStorage.getItem('flag55')),
      flag66:JSON.parse(window.localStorage.getItem('flag66')),
    }
  },
  methods:{
    changeEdit() {
      this.flag = !this.flag;
      },
    changeStatus(e){
      if(e.target.nodeName == 'BUTTON'){
        if(!e.target.className&&e.target.parentNode.id=='f11'){
          var flag11 = !JSON.parse(window.localStorage.getItem('flag11'))
          this.flag11 = !this.flag11
          window.localStorage.setItem('flag11', flag11)

        }
        if(!e.target.className&&e.target.parentNode.id=='f22'){
          var flag22 = !JSON.parse(window.localStorage.getItem('flag22'))
          this.flag22 = !this.flag22
          window.localStorage.setItem('flag22', flag22)
        }
        if(!e.target.className&&e.target.parentNode.id=='f33'){
          var flag33 = !JSON.parse(window.localStorage.getItem('flag33'))
          this.flag33 = !this.flag33
          window.localStorage.setItem('flag33', flag33)
        }
        if(!e.target.className&&e.target.parentNode.id=='f44'){
          var flag44 = !JSON.parse(window.localStorage.getItem('flag44'))
          this.flag44 = !this.flag44
          window.localStorage.setItem('flag44', flag44)
        }
        if(!e.target.className&&e.target.parentNode.id=='f55'){
          var flag55 = !JSON.parse(window.localStorage.getItem('flag55'))
          this.flag55 = !this.flag55
          window.localStorage.setItem('flag55', flag55)
        }
        if(!e.target.className&&e.target.parentNode.id=='f66'){
          var flag66 = !JSON.parse(window.localStorage.getItem('flag66'))
          this.flag66 = !this.flag66
          window.localStorage.setItem('flag66', flag66)
        }
      }
    },
    defaultStatus(){
      
      }

  },
  mounted(){
    if (window.localStorage.getItem('flag11')=='') {
      window.localStorage.setItem('flag11', true)
    }
    if (window.localStorage.getItem('flag22')=='') {
      window.localStorage.setItem('flag22', true)
    }
    if (window.localStorage.getItem('flag33')=='') {
      window.localStorage.setItem('flag33', true)
    }
    if (window.localStorage.getItem('flag44')=='') {
      window.localStorage.setItem('flag44', true)
    }
    if (window.localStorage.getItem('flag55')=='') {
      window.localStorage.setItem('flag55', true)
    }
    if (window.localStorage.getItem('flag66')=='') {
      window.localStorage.setItem('flag66', true)
    }
  }

}
</script>

<style>
  .management-box{
    background: #e6e6e6;
    padding: 1.0666rem 1.0666rem 2.333rem 1.0666rem;
    width: 100%;
    font-size: 0.8333rem;
    line-height: 0.9333rem;
    color: #0d0101;
    font-weight: bold;
    font-family: "Gill Sans BQ Regular"; 
    text-align: center;
    border-spacing: 0 5px;
    position: relative;
  }
  .management-box img{
    width: 5rem;
    height: 4.4rem;
  }
  .management-box tr{
    margin-top: 5px;
  }
  .management-box tr td:nth-child(1){
    width: 20%;
  }
  .management-box tr td:nth-child(2){
    width: 35%;
  }
  .editbtnbg{
    background:#e6e6e6;
  }
  .editbtn{
    font-size: 1rem;
    text-align: center;
    background: #fff;
    cursor: pointer;
    border-radius: 3px;
    width: 6.666rem;
    height: 1.666rem;
    line-height: 1.666rem;
    margin: 0 auto;
  }
  .editbtn a{
    
    color: #d01b1b;
  }
  .management-box button{
    outline: none;
    width: 4.333rem;
    height: 1.333rem;
    font-size: 0.833rem;
    line-height: 0.933rem;
    color: #0d0101;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  .management-box button:hover{
    background: royalblue;
  }
  .management-box button:first-child{
    margin-right: 50px;
  }
  .active1{
    background: blue;
  }
  @media screen and (max-width: 767px){

  }
</style>