import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Contactus from '../views/Contactus'
import OnlineOrder from '../views/OnlineOrder'
import Location from '../views/Location'
import Management from '../views/Management'
import OnlineReserve from '../views/OnlineReserve'
import PaymentSuccess from '../views/PaymentSuccess'
import Login from '../views/login'
Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/contactus',
        name: 'Contactus',
        component: Contactus
    },
    {
        path: '/location',
        name: 'Location',
        component: Location
    },
    {
        path: '/onlineorder',
        name: 'OnlineOrder',
        component: OnlineOrder
    },
    {
        path: '/management',
        name: 'Management',
        component: Management,
        beforeEnter: (to, from, next) => {
            console.log(123);
            var token = JSON.parse(window.sessionStorage.getItem('token'))
            if (!token) {
                alert('Please login first')
                next('/login')

            } else if (token.superuser) {
                next()
            } else if (!token.superuser) {
                alert('Only managers can enter')

            }
        }
    },
    {
        path: '/reserve/:id',
        name: 'reserve',
        component: OnlineReserve
    },
    {
        path: '/paymentsuccess',
        name: 'PaymentSuccess',
        component: PaymentSuccess
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },



]

const router = new VueRouter({
    mode: 'history',
    linkActiveClass: 'actived',
    routes,
})

export default router