<template>
  <div class="register2">
    <div class="register2-inner">
      <form action="">
        <p>USERNAME <i class="iconfont icon-user iconcom"></i><input type="text" @focus="activeColor" @blur="deactiveColor" class="i1" v-model="username"></p>
        <p>PASSWORD <i class="iconfont icon-yuechi iconcom"></i><input type="password" @focus="activeColor" @blur="deactiveColor" class="i2" v-model="password"></p>
        <div><input type="submit" value="LOGIN" id="login" @click.prevent="gettoken"></div>
        <g-signin-button style="margin:0 0.666rem 0 1.333rem;" :params="googleSignInParams" @success="onSignInSuccess" @error="onSignInError"></g-signin-button>
        <fb-signin-button style="margin:0 0.666rem;" :params="fbSignInParams" @success="onSignInSuccessf" @error="onSignInErrorf"></fb-signin-button>
        <!-- <button v-facebook-login-button="appId" class="facebook-login-button">Continue with Facebook</button> -->
        <!-- <router-link to="#"><img src="../assets/images/1200px-Twitter_bird_logo_2012.svg.png" alt=""></router-link> -->
        <!-- <router-link to="#"><img src="../assets/images/FB.png" alt=""></router-link> -->
        <!-- <router-link to="#"><img src="../assets/images/INS.jpg" alt=""></router-link> -->
        <!-- <router-link to="#"><img src="../assets/images/INS.jpg" alt=""></router-link> -->
        <div class="github-login" style="margin:0 1.666rem;" @click="auth"></div>
      </form>
    </div>
    <div class="loading-wrapper" v-if="flag2">
      <div class="loading-inner">
        <div class="loading"></div>
        <div>be logging in...</div>
      </div>
    </div>
  </div>
</template>

<script>
import jsonwebtoken from 'jsonwebtoken'
export default {
    
    data(){
        return {
          username:'',
          password:'',
          flag1:'login',
          flag2:false,
          googleSignInParams: {
          client_id: '179249781124-51kdjn0ublbugemobr2g2nq0de9tmnpd.apps.googleusercontent.com'
        },
          fbSignInParams: {
          scope: 'email,user_likes',
          return_scopes: true
        }
          
        }
    },

    methods:{
    // google 函数
    onSignInSuccess (googleUser) {
      //`googleUser` is the GoogleUser object that represents the just-signed-in user.
      //保存用户token
      
      // See https://developers.google.com/identity/sign-in/web/reference#users
      //保存用户名
      const profile = googleUser.getBasicProfile() // etc etc
      var token_id = googleUser.wc.id_token
      var username = profile.Ad
      username.length>6?username = username.slice(0,6):null
      window.sessionStorage.setItem('token', JSON.stringify({token_id,token_name:username,superuser:false}))
      alert('login success')
      this.$router.push('/')
    },
    onSignInError (error) {
      // `error` contains any error occurred.
      console.log('OH NOES', error)
    },
    
    // facebook 函数
    onSignInSuccessf (response) {
        var token_id = response.authResponse.accessToken;
        var username = response.authResponse.userID;
        username.length>6?username = username.slice(0,6):null
        window.sessionStorage.setItem('token', JSON.stringify({token_id,token_name:username,superuser:false}))
        alert('login success')
        this.$router.push('/')
    },
    onSignInErrorf (error) {
      console.log(error);
    },
    

    activeColor(e){
      e.target.style.border = '1px solid #5bc6bd'
      e.target.previousSibling.style.color = '#5bc6bd'
    },
    deactiveColor(e){
      e.target.style.border = '1px solid #d8d6d6'
      e.target.previousSibling.style.color = '#776f6f'
    },
    changestatus(){
      this.flag1 = !this.flag1;
    },
    gettoken(){
      
    if (this.username!=''&&this.password!='') {
      if (this.username.length>20||this.password.length>20) {
        alert('Account password length is greater than 20')
      } else {
        this.$http.post(
          'http://127.0.0.1:80/login',
          {
            username:this.username,
            password:this.password
            },
        ).then(response =>{
          if (response.data.token) {
            window.sessionStorage.setItem('token',JSON.stringify({token_id:response.data.token,superuser:response.data.superuser}))
            alert('Login success')
            this.$router.push('/')
          }else{
            alert(response.data.message)
          }
        }).catch(function (error) {
          console.log(error);
        })
      }
    }else{
      alert('The username or password cannot be empty');
    }
  },
  auth(){
    window.location.href = 'https://github.com/login/oauth/authorize?client_id=ce277b644432158c9aa3'

  }
  },
  created(){
    
  },
  mounted(){
    this.$emit('flag1', this.flag1);

    // this.$http.get('/api/jod/categories',{
    //   headers: {
    //     "x-rapidapi-host": "jokes.p.rapidapi.com",
    //     "x-rapidapi-key": "a545ab5d4cmshd27e32c92a7ef77p1b7b2ajsna5e1a367fc97"
    //     }
    //   }).then(res=>{
    //     console.log(res);
    //   }).catch(err=>{
    //     console.log(err);
    //   })
    
    var code = this.$route.query.code
    if (code && code!=window.localStorage.getItem(code)) {
      //console.log('正在发送请求');
    window.localStorage.setItem(code,code)
      let tokenRespone = this.$http.post(
        'http://localhost:80/auth',
        {code}
      )
      this.flag2 = true
      tokenRespone.then(res=>{
        console.log(res);
        if (res.data.message === 'ok') {
          let {token_id, username:token_name,superuser} = res.data;
          token_name.length>6?token_name = token_name.slice(0,6):null
          window.sessionStorage.setItem('token',JSON.stringify({token_id,superuser,token_name}))
          alert('Login Success')
          this.$router.push('./')
          this.flag2 = false
        }else if (res.data === 'failed') {
          alert('login failed')
          this.flag2 = false
        }else if(res.data === 'The request timeout'){
          alert('The request timeout')
          this.flag2 = false
        }else if(res.data === 'bad_verification_code' || res.data ==='unknow error'){
          alert('Please log in again')
          this.flag2 = false
        }
      })
      tokenRespone.catch(err=>{
        alert('login failed')
        this.flag2 = false
        console.log(err);
      })
    }
  },
  beforeDestroy(){
      this.$emit('flag1', 'show')
  }
}
</script>

<style>
