<template>
    <header>
        <div class="joke" title="joke" @mouseenter="h" @mouseleave="s">
          <h4 align='center' v-if="flag1">{{ joketitle }}</h4>
          <p v-for="joke in jokecontent" :key=jokecontent[joke] v-show="flag2">{{ joke }}</p>
          </div>
        <div class="img animated fadeInLeft" @click.prevent="$router.push('/')" title="Home page">
          <img src="../assets/images/tuancake_11369287.jpg" class="img1" alt="">
        </div>
        <div class="header-serach">
        <div class="serach-inner">
            <select name="" id="" class="select">
            <option value="1">ALL</option>
        </select><input type="text" class="input1"><i class="iconfont icon-sousuo"></i>
        </div>
        </div>
        <div class="phone-number">
        <p class="pn">Phone number</p>
        <p class="number">888-88888888</p>
        </div>
    </header>
</template>

<script>
export default {
  data(){
    return {
      data:'',
      img2:'',
      joketitle:'',
      jokecontent:[],
      flag1:false,
      flag2:false,

    }
  },
  methods:{
    h(){
      //document.getElementsByClassName('img1')[0].src = require('../assets/images/whitebgi.png')
      //document.getElementsByClassName('img1')[0].style.display = 'none'
    },
    s(){
      //document.getElementsByClassName('img1')[0].src = require('../assets/images/tuancake_11369287.jpg')
      //document.getElementsByClassName('img1')[0].style.display = 'block'
    }
  },
  created(){
    
    this.$http.get('https://jokes.p.rapidapi.com/jod/categories',{
      headers: {
        "x-rapidapi-host": "jokes.p.rapidapi.com",
		    "x-rapidapi-key": "a545ab5d4cmshd27e32c92a7ef77p1b7b2ajsna5e1a367fc97"
      }
    }).then(response=>{
      if (response.status == 200){
        var categories = response.data.contents.categories.map(item=>{
          return item.name
        });
        return categories[Math.floor(Math.random()*categories.length)]
      }
    })
    .then(category=>{
      return this.$http.get(`https://jokes.p.rapidapi.com/jod?category=${category}`,{
        headers: {
          "x-rapidapi-host": "jokes.p.rapidapi.com",
          "x-rapidapi-key": "a545ab5d4cmshd27e32c92a7ef77p1b7b2ajsna5e1a367fc97"
        }
      })
    })
    .then(data=>{
      if (data.status == 200) {
        var joke = data.data.contents.jokes[0].joke;
        this.joketitle = joke.title;
        this.jokecontent = joke.text.split('\r\n')
        this.flag1 = true
        this.flag2 = true
      }
    })
    .catch(error=>{
      console.log(error);
    })
  }
}
</script>

<style scoped>
/*header start*/
header{
  height: 3.33rem;
  display: flex;
}
header .joke{
  cursor: pointer;
  z-index: 10;
  padding: 1% 0 0 1%;
  width: 23%;
  overflow: hidden;
}
header .joke:hover{
  transform-origin:left top;
  transform: scale(1.2);
  overflow: unset;
  background: #eee;
}
header .joke p{
  text-overflow: ellipsis;
  overflow: hidden;  
  padding-left: 5%;

}
header .joke:hover p{
  overflow: unset;
  background: #eee;
}
header .img{
  margin-left: 5%;
  width: 4.5rem;
  height: 3.33rem;
  overflow: hidden;
}
header .img:hover{
  animation: su-pudding 1s;
}
header .img img{
  width: 100%;
  height: 100%;
  transform: scale(1.5);
  cursor: pointer;
}
.header-serach{
  margin-left: 4rem;
}
.header-serach .serach-inner{
  display: flex;
  margin-top: 1rem;
}
.header-serach .serach-inner .select{
  color: #666666;
  border-radius: 8px 0 0 8px;
  padding-left: 0.833rem;
  width: 3rem;
  height: 1.266rem;
  outline: none;
  border-right: none;
  border-color: #cccccc;
}
.header-serach .serach-inner .input1{
  outline: none;
  height: 1.266rem;
  width: 4.666rem;
  border: 1px solid #cccccc;
  border-right: none;
}
.header-serach .serach-inner i{
  cursor: pointer;
  color: #ffffff;
  border-radius: 0 8px 8px 0;
  box-sizing: border-box;
  padding: 0.333rem 0 0 0.733rem;
  font-size: 0.63rem;
  width: 2.4rem;
  border: 1px solid #cccccc;
  background: #17ba7a;
}
.header-serach .serach-inner i:hover{
  animation: su-pudding 1s;
}
.phone-number{
  font-size: 0.5rem;
  margin-left: 2.9rem;
}
.phone-number .pn{
  margin-top: 1rem;
  color: #666666;

}
.phone-number .number{
  margin-top: 0.1rem;
  color: #36bf85;
  
}
@media screen and (max-width: 992px){
  .phone-number .pn{
    margin-top: 0;
  }
  .phone-number .number{
    margin-top: 0;
  }
}
@media screen and (max-width: 767px){
  header {
    /* display: none; */
  }
  header .joke{
    display: none;
  }
  .header-serach .serach-inner .select{
    height: 1.55rem;
  }
  .header-serach .serach-inner .input1{
    height: 1.55rem;
  }
  .header-serach{
  margin-left: 1rem;
  }
  .phone-number .pn{
  margin-top: 0;
  color: #666666;
  }
  .phone-number .number{
    margin-top: 0;
  }
}
/*header end */
</style>