<template>
  <div>
    <HomeBgimg />
    <DoubleLoge />
    <div class="content">
      <div class="detail-left">
        <p>PRODUCT DETAILS</p>
        <img :src="cake.imgurl" alt="">
      </div>
      <div class="detail-right">
        <p class="p1 pcom">{{ cake.cakename }}</p>
        <p class="p2 pcom">({{ cake.per }})</p>
        <p class="p3 pcom" v-if="flag">CAKE INGREDIENTS: </p>
        <p class="p4 pcom" v-if="flag">{{ cake.cake_ingr }}</p>
        <div class="number pcom">
          <span class="add-subtract-input">
            <img src="../assets/images/subtraction1.png" alt="" @click='subtraction'>
            <input type="text" class="num" v-model="number" name="num" />
            <img src="../assets/images/add1.png" alt="" @click="add">
          </span>
        </div>
        <div class="shop-car" v-if="flag" @click="showpayment">
          <router-link to="#" ><img src="../assets/images/shopcar.jpg" alt="" ></router-link>
        </div>
        <div class="pay-module" v-if="!flag">
          <form action="" class="rec-information">
            <p>Recipient information:</p>
            <p>Name: <input type="text" name="username" style="margin-left:1em;" v-model="username"></p>
            <p>Address: <input type="text" name="address" v-model="address"></p>
            <p>Payment method:</p>
            <div class="payimg-group" @click="changPayimg">
              <img src="../assets/images/blog173_1.png" alt="" style="width:30%;height:1.6rem;box-sizing: border-box;cursor: pointer;">
              <img src="../assets/images/d19784-44-622994-0-1.png" alt="" style="width:26%;height:2.333rem;box-sizing: border-box;cursor: pointer;">
              <img src="../assets/images/flywire-logo.jpg" alt="" style="width:26%;height:2.333rem;margin-right:0;box-sizing: border-box;cursor: pointer;">
            </div>
            <input type="submit" value="Submit Order" id="submitbtn" @click.prevent="toPayment">
          </form>
          </div>
          <div class="mask-wrapper" v-show="flag1">
            <div class="payinfo">
              <div class="close-wrapper" title="close" @click="h">
                <i class="iconfont icon-baseline-close-px" id="close-btn"></i>
              </div>
              <div class="detailinfo">
                <h2 align='center'>Order Information</h2>

                <table id="tb">
                  <tr>
                    <td class="align-r">CAKE:</td>
                    <td class="align-l">{{ cake.cakename }}</td>
                  </tr>
                  <tr>
                    <td class="align-r">NUMBER:</td>
                    <td class="align-l">{{ number }}</td>
                  </tr>
                  <tr>
                    <td class="align-r">NAME:</td>
                    <td class="align-l">{{ username }}</td>
                  </tr>
                  <tr>
                    <td class="align-r">ADDRESS:</td>
                    <td class="align-l">{{ address }}</td>
                  </tr>
                  <tr>
                    <td class="align-r">PAYMENT METHOD:</td>
                    <td class="align-l paymethod"></td>
                  </tr>
                  <tr>
                    <td colspan="2" style="padding:20px;">
                      <input type="submit" value="Submit Order" id="sub2" @click.prevent="toSuccess">
                    </td>
                  </tr>
                </table>
              </div>
            </div>
        </div>
      </div>
      
    </div>
  </div>
</template>

<script>
import HomeBgimg from '../components/HomeBgimg'
import DoubleLoge from '../components/DoubleLoge'
export default {
  components:{
    HomeBgimg,
    DoubleLoge,
  },
  data(){
    return {
      number:0,
      id:"",
      cake:{},
      flag:true,
      flag1:false,
      username:'',
      address:'',
      cakearr:[
        {'id':1, 'imgurl':require("../assets/images/online_melaleuca.jpg"), 'cakename': 'MELALEUCA', 'per':"4--6PER",'cake_ingr':'LOW-GLUTEN FLOUR,HIGH-GLUTEN FLOUR,WHITE SUGAR,EGGS,BUTTER,YEAST,CHEESE.'},
        {'id':2, 'imgurl':require("../assets/images/online_cheesecake.jpg"), 'cakename': 'CHEESECAKE', 'per':"4--6PER",'cake_ingr':'CREAM CHEESE,LOW-GLUTEN FLOUR,CORNSTARCH,EGGS,SUGAR,LIGHT CREAM, VEGE-TABLE OIL.'},
        {'id':3, 'imgurl':require("../assets/images/online_cupcake.jpg"), 'cakename': 'CUPCAKE', 'per':"2--3PER",'cake_ingr':'SUGAR,BUTTER,FLOUR,BAKING SODA,BAKING POWDER,EGG,MILK,VANILLA.'},
        {'id':4, 'imgurl':require("../assets/images/online_toiletpapercake.jpg"), 'cakename': 'CREATIVE TOILET PAPER CAKE', 'per':"4--6PER",'cake_ingr':'SUGAR,BUTTER,FLOUR,BAKING SODA,BAKING POWDER,EGG,MILK,VANILLA EXTRACT'},
        {'id':5, 'imgurl':require("../assets/images/online_tiane.jpg"), 'cakename': 'SPECIAL CUSTOM SWANCAKE', 'per':"4--6PER",'cake_ingr':'SUGAR,BUTTER,FLOUR,BAKING SODA,BAKING POWDER,EGG,MILK,VANILLA EXTRACT.'},
        {'id':6, 'imgurl':require("../assets/images/online_shuiguocake.jpg"), 'cakename': 'FRUIT BIRTHDAY CAKE', 'per':"4--6PER",'cake_ingr':'SUGAR,BUTTER,FLOUR,BAKING SODA,BAKING POWDER,EGG,MILK,VANILLA EXTRACT.'},
      ]
    }
  },
  methods:{
    subtraction(){
      if(this.number<1){
        return;
      }else{
        this.number -= 1;
      }
    },
    add(){
      if(this.number>999){
        return
      }else{
        this.number +=1;
      }
    },
    showpayment(){
      this.flag = !this.flag
    },
    toPayment(){
      var anode = null
      var cnodes = document.getElementsByClassName('payimg-group')[0].childNodes
      cnodes.forEach((item,index)=>{
        if (item.className =='payimg') {
          anode = index
          let newitem = item.cloneNode(true)
          document.getElementsByClassName('paymethod')[0].innerHTML = ''
          document.getElementsByClassName('paymethod')[0].appendChild(newitem)
        }
      })
      
      if(this.number===0){
        alert('Please select quantity')
      }else if(this.username == '' || this.address == ''){
        alert('Information cannot be empty')
      }else if (this.username.length > 100 || this.address.length > 500) {
        alert('The message should not be too long')
      }else if(anode===null){
        alert('Please choose the mode of payment')
      }else {

        this.flag1 = true
      }
    },
    h(){
      this.flag1 = false
    },
    toSuccess(){
      alert('successful reservation')
      this.$router.push('/paymentsuccess')
    },
    changPayimg(e){
      document.getElementsByClassName('payimg-group')[0].childNodes.forEach((item)=>{
        item.className = ''
      })
      if (e.target.tagName == 'IMG') {
        e.target.className = 'payimg'
      }
    }
  },
  mounted:function(){
    this.id = this.$route.params.id
    for (let index = 0; index < this.cakearr.length; index++) {
      if (this.cakearr[index].id == this.id) {
        this.cake = this.cakearr[index]
      }
      
    }
  }
}

</script>

<style scoped>
  .content{
    box-sizing: border-box;
    padding: 1.333rem 2.5333rem;
    width: 100%;
    background: #e6e6e6;
    display: flex;
    justify-content: space-around;
  }
  .content .detail-left{
    width: 50%;
  }
  .content .detail-left img{
    display: block;
    margin: 0 auto;
    margin-top: 1rem;
    width: 90%;
    height: 70%;
  }
  .content .detail-left p{
    font-style: italic;
    font-size: 1rem;
    line-height: 2.333rem;
    color: #300f0f;
    font-weight: bold;
    font-family: "Myriad Pro";
    text-align: center;
  }
  .content .detail-right{
    width: 40%;
  }
  .content .detail-right .pcom{
    text-align: center;
  }
  .content .detail-right .p1{
    font-style: italic;
    font-size: 1rem;
    line-height: 1.666rem;
    color: #dda8f6;
    font-weight: bold;  
    font-family: "Myriad Pro";
    text-align: center;
  }
  .content .detail-right .p2{
    font-size: 1rem;
    line-height: 1.666rem;
    color: #dda8f6;
  }
  .content .detail-right .p3{
    font-style: italic;
    margin-top: 0.666rem;
    font-size: 1rem;
    color: #300f0f;
    font-weight: bold;
    font-family: "Myriad Pro";
  }
  .content .detail-right .p4{
    margin: 0.666rem 0 0 15%;
    width: 80%;
    text-align: left;
    font-weight: bold;
    font-style: italic;
    color: #300f0f;
    word-break: break-word;
  }
  .content .detail-right .number{
    margin: 2rem 0 2rem 0;
  }
  .content .detail-right .number .add-subtract-input img{
    cursor: pointer;
    display: inline-block;
    width: 2rem;
    height: 2rem;
    vertical-align: middle;
    border-radius: 50%;
  }

  .content .detail-right .number .add-subtract-input .num{
    font-size: 0.8rem;
    text-align: center;
    outline: none;
    border: none;
    background: #fff;
    width: 3rem;
    height: 2.1333rem;
    vertical-align: middle;
    margin: 0 0.333rem;
  }
  .content .detail-right .shop-car{
    margin: 0 auto;
    width: 2.666rem;
    height: 2rem;
    
  }
  .content .detail-right .shop-car img{
    width: 100%;
    height: 100%;
  }
  .content .detail-right .pay-module {
    font-weight: bold;
    letter-spacing: 2px;
    color: #050606;
    font-family: "Arial";
    
  }
  .content .detail-right .pay-module .rec-information img{
    vertical-align: middle;
    margin-right: 2%;
  }
  .content .detail-right .pay-module .rec-information input{
    display: inline-block;
    outline: none;
    border:2px solid #000000;
    border-radius: 7px;
    width: 8.333rem;
    height: 1rem;
    font-size: 0.666rem;
    padding: 0.0666rem 0.0666rem;
  }
  
  .content .detail-right .pay-module .rec-information p{
    font-size: 0.5rem;
    margin-bottom: 0.666rem;
  }
  .content .detail-right .pay-module .rec-information #submitbtn{
    cursor: pointer;
    display: block;
    margin: 1rem auto;
    width: 6.666rem;
    height: 1.533rem;
  }
  .payimg{
    border: 2px solid black;
    
  }
  .mask-wrapper{
    position: fixed;
    left: 0;
    bottom: 0;
    top: 0;
    right: 0;
    background: rgba(0,0,0,0.3);
    z-index: 100;
  }
  .mask-wrapper .payinfo{
    margin: 0 auto;
    width: 90%;
    height: 100%;
    background: #fff;
    overflow: hidden;
  }
  .mask-wrapper .payinfo .close-wrapper{
    cursor: pointer;
    padding: 10px;
    float: right;
  }
  .mask-wrapper .payinfo .detailinfo{
    
    margin: 10% 15% 0 15%;
  }
  .mask-wrapper .payinfo .detailinfo table{
    border-collapse: collapse;
  }
  .mask-wrapper .payinfo .detailinfo tr td{
    font-size: 0.666rem;
    line-height: 2em;
    border: 1px solid #e6e6e6;
  }
  .align-r{
    padding: 0.333rem;
    text-align: right;
  }
  .align-l{
    padding: 0.333rem;
  }
  .mask-wrapper .payinfo .close-wrapper:hover{
    color: blue;
  }
  .mask-wrapper .payinfo .close-wrapper i{
    font-size: 1rem;
  }
  #sub2{
    display:block;
    cursor:pointer;
    margin:0 auto;
    padding:0.333rem 0.666rem;
    border:1px solid black;
    background: beige;
  }
  #sub2:hover{
    background: #e6e6e6;
  }
  @media screen and (max-width: 767px){
    .content .detail-right .p4{
      font-size: 0.1rem;
    }
    .content .detail-right .pay-module .rec-information input{
      height: 1.5rem;
    }
    .content .detail-left img{
      height: 60%;
    }
  }
</style>